import React, { useEffect, useState } from 'react';
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { getDatabase, ref, onValue, remove, set } from "firebase/database";
import { Container, Paper, Typography, List, ListItem, ListItemText, Button, Dialog, DialogTitle, DialogContent, DialogActions, Accordion, AccordionSummary, AccordionDetails } from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const UserProfilePage = () => {
  const [user, setUser] = useState(null);
  const [userData, setUserData] = useState(null);
  const [appointments, setAppointments] = useState([]);
  const [services, setServices] = useState([]);
  const [selectedAppointment, setSelectedAppointment] = useState(null);
  const [confirmationDialogOpen, setConfirmationDialogOpen] = useState(false);

  const auth = getAuth();
  const database = getDatabase();

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (firebaseUser) => {
      if (firebaseUser) {
        setUser(firebaseUser);
        const userRef = ref(database, `users/${firebaseUser.uid}`);
        onValue(userRef, (snapshot) => {
          const data = snapshot.val();
          if (data) {
            setUserData(data);
            if (data.profile.type === 'business_owner' && data.businessDetails && data.businessDetails.servicesOffered) {
              fetchServices(data.businessDetails.servicesOffered);
            } else if (data.appointments) {
              fetchAppointments(data.appointments);
            }
          }
        });
      } else {
        setUser(null);
        setUserData(null);
        setAppointments([]);
        setServices([]);
      }
    });

    return () => unsubscribe();
  }, [auth, database]);

  const fetchAppointments = (appointmentIds) => {
    const fetchedAppointments = [];

    appointmentIds.forEach((appointmentId) => {
      const appRef = ref(database, `appointments/${appointmentId}`);
      onValue(appRef, (appSnapshot) => {
        const appointmentData = appSnapshot.val();
        if (appointmentData) {
          const serviceRef = ref(database, `services/${appointmentData.serviceId}`);
          onValue(serviceRef, (serviceSnapshot) => {
            const serviceData = serviceSnapshot.val();
            if (serviceData) {
              const providerRef = ref(database, `users/${serviceData.ownerId}`);
              onValue(providerRef, (providerSnapshot) => {
                const providerData = providerSnapshot.val();
                if (providerData && providerData.profile.name) {
                  const updatedAppointment = {
                    ...appointmentData,
                    key: appointmentId,
                    serviceName: serviceData.name,
                    provider: providerData.profile.name,
                  };
                  fetchedAppointments.push(updatedAppointment);
                  setAppointments([...fetchedAppointments]);
                }
              });
            }
          });
        }
      });
    });
  };

  const fetchServices = (serviceIds) => {
    Promise.all(serviceIds.map(id => {
      return new Promise((resolveService) => {
        const serviceRef = ref(database, `services/${id}`);
        onValue(serviceRef, (snap) => {
          const serviceData = snap.val();
          if (serviceData) {
            if (serviceData.bookings && serviceData.bookings.length > 0) {
              // Fetch all user names asynchronously and wait for all to complete
              Promise.all(serviceData.bookings.map(booking => {
                return new Promise((resolveUser) => {
                  const userRef = ref(database, `users/${booking.userId}`);
                  onValue(userRef, (userSnapshot) => {
                    resolveUser({ ...booking, userName: userSnapshot.val().profile.name });
                  });
                });
              })).then(completeBookings => {
                resolveService({ ...serviceData, bookings: completeBookings });
              });
            } else {
              resolveService(serviceData); // Resolve immediately if no bookings
            }
          } else {
            resolveService(null); // Resolve with null if no service data
          }
        });
      });
    })).then(fetchedServices => {
      setServices(fetchedServices.filter(service => service !== null));
    });
  };
  

  const handleUnbookAppointment = () => {
    if (selectedAppointment) {
      const appointmentRef = ref(database, `appointments/${selectedAppointment.key}`);
      remove(appointmentRef)
        .then(() => {
          // Remove appointment from user's appointments array
          const updatedAppointments = userData.appointments.filter(id => id !== selectedAppointment.key);
          setAppointments(prevAppointments => prevAppointments.filter(appointment => appointment.key !== selectedAppointment.key));
          const userRef = ref(database, `users/${user.uid}`);
          set(userRef, { ...userData, appointments: updatedAppointments });
  
          // Update the service data by removing the specific booking entry
          const serviceRef = ref(database, `services/${selectedAppointment.serviceId}`);
          onValue(serviceRef, (serviceSnapshot) => {
            let serviceData = serviceSnapshot.val();
            if (serviceData && serviceData.bookings) {
              const updatedBookings = serviceData.bookings.filter(booking => booking.userId !== user.uid || booking.date !== selectedAppointment.date || booking.time !== selectedAppointment.time);
              set(ref(database, `services/${selectedAppointment.serviceId}/bookings`), updatedBookings);
            }
          });
        })
        .catch(error => {
          console.error("Error removing appointment: ", error);
        });
      setConfirmationDialogOpen(false);
    }
  };  

  const openConfirmationDialog = (appointment) => {
    setSelectedAppointment(appointment);
    setConfirmationDialogOpen(true);
  };

  return (
    <Container component="main" maxWidth="sm">
      <Paper elevation={6} sx={{ padding: 3, marginTop: 8 }}>
        <Typography component="h1" variant="h5">
          User Profile
        </Typography>
        {user && userData ? (
          <>
            <Typography variant="body1">Name: {userData.profile.name}</Typography>
            <Typography variant="body1">Email: {user.email}</Typography>
            <Typography variant="body1">User Type: {userData.profile.type}</Typography>
            {userData.profile.type === 'business_owner' && userData.businessDetails && userData.businessDetails.servicesOffered && (
              <>
                <Typography variant="body1">Business Name: {userData.businessDetails.businessName}</Typography>
                <Typography variant="h6" sx={{ mt: 2 }}>
                  Services Offered:
                </Typography>
                {services.map((service, index) => (
                  <Accordion key={index}>
                    <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                      <Typography>{service.name}</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      <List>
                        <ListItem>
                          <ListItemText primary={`Price: $${service.price}`} />
                        </ListItem>
                        <ListItem>
                          <ListItemText primary={`Category: ${service.category}`} />
                        </ListItem>
                        <ListItem>
                          <ListItemText primary={`Users signed up: ${service.bookings ? service.bookings.length : 0}`} />
                        </ListItem>
                        <ListItem>
                        <ListItemText primary="Booked Users:" />
                        {service.bookings && service.bookings.map((booking, index) => (
                          <ListItem key={index}>
                            <ListItemText primary={`User: ${booking.userName}, Date: ${booking.date}, Time: ${booking.time}`} />
                          </ListItem>
                        ))}
                      </ListItem>
                      </List>
                    </AccordionDetails>
                  </Accordion>
                ))}
              </>
            )}

            <Typography variant="h6" sx={{ mt: 2 }}>
              Appointments:
            </Typography>
            <List>
              {appointments.length > 0 ? (
                appointments.map((appointment, index) => (
                  <ListItem key={index}>
                    <ListItemText
                      primary={`Service: ${appointment.serviceName}`}
                      secondary={
                        <>
                          <Typography component="span" variant="body2">Date: {appointment.date}, Time: {appointment.time}</Typography><br/>
                          <Typography component="span" variant="body2">Provider: {appointment.provider}</Typography>
                        </>
                      }
                    />
                    <Button onClick={() => openConfirmationDialog(appointment)}>Unbook</Button>
                  </ListItem>
                ))
              ) : (
                <Typography variant="body1">No appointments booked.</Typography>
              )}
            </List>
          </>
        ) : (
          <Typography variant="body1">
            No user data available. Please log in.
          </Typography>
        )}
      </Paper>

      <Dialog open={confirmationDialogOpen} onClose={() => setConfirmationDialogOpen(false)}>
        <DialogTitle>Confirm Unbooking</DialogTitle>
        <DialogContent>
          Are you sure you want to unbook this appointment?
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setConfirmationDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleUnbookAppointment}>Confirm</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default UserProfilePage;
