import React, { useState } from 'react';
import { Container, Paper, Typography, TextField, Checkbox, FormControlLabel, Button, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { initializeApp, getApps, getApp } from 'firebase/app';
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import { getDatabase, ref, set } from 'firebase/database';

// Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyA_uX33ajqsMKAfp7LeP9u57AdBfcuQW2w",
  authDomain: "bookit-v2.firebaseapp.com",
  projectId: "bookit-v2",
  storageBucket: "bookit-v2.appspot.com",
  messagingSenderId: "24786135640",
  appId: "1:24786135640:web:a10a629de292aa2b97f82d",
  measurementId: "G-HHRKZL7JJR"
};

// Initialize Firebase only once
const app = !getApps().length ? initializeApp(firebaseConfig) : getApp();
const auth = getAuth(app);
const db = getDatabase(app);

const SignUpPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [isBusinessOwner, setIsBusinessOwner] = useState(false);
  const [businessName, setBusinessName] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleCheckboxChange = (event) => {
    setIsBusinessOwner(event.target.checked);
  };

  const handleSignUp = async (event) => {
    event.preventDefault();

    if (!email || !password || !name || !phone || (isBusinessOwner && !businessName)) {
      setError('Please fill in all required fields.');
      return;
    }

    try {
      // Create user in Firebase Authentication
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;
      
      // Create user profile data
      const userProfile = {
        email: user.email,
        profile: {
          name: name,
          phone: phone,
          type: isBusinessOwner ? 'business_owner' : 'customer',
        },
        appointments: [],
      };

      // If user is a business owner, add business details
      if (isBusinessOwner) {
        userProfile.businessDetails = {
          businessName,
          // Assuming a list of service IDs will be added later as they are created
          servicesOffered: []
        };
      }

      // Write the user profile to Realtime Database under the 'users' node
      await set(ref(db, 'users/' + user.uid), userProfile);

      console.log('User signed up successfully');
      navigate('/'); // Navigate to the dashboard or home page
    } catch (error) {
      console.error('Error signing up:', error);
      setError(error.message);
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Paper elevation={6} sx={{ marginTop: 8, display: 'flex', flexDirection: 'column', alignItems: 'center', padding: 3 }}>
        <Typography component="h1" variant="h5">
          Sign Up
        </Typography>
        <Box component="form" onSubmit={handleSignUp} noValidate sx={{ mt: 1 }}>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="name"
            label="Full Name"
            name="name"
            autoComplete="name"
            autoFocus
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="phone"
            label="Phone Number"
            name="phone"
            autoComplete="tel"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="new-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <FormControlLabel
            control={<Checkbox checked={isBusinessOwner} onChange={handleCheckboxChange} name="isBusinessOwner" />}
            label="Business Owner?"
          />
          {isBusinessOwner && (
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              name="businessName"
              label="Business Name"
              type="text"
              id="businessName"
              autoComplete="business-name"
              value={businessName}
              onChange={(e) => setBusinessName(e.target.value)}
            />
          )}
          {error && <Typography variant="body2" color="error">{error}</Typography>}
          <Button
            type="submit"
            fullWidth
            variant="contained"
            sx={{ mt: 3, mb: 2 }}
          >
            Sign Up
          </Button>
        </Box>
      </Paper>
    </Container>
  );
};

export default SignUpPage;
