import React, { useState, useEffect } from 'react';
import { AppBar, Toolbar, IconButton, Typography, Button, Drawer, List, ListItemButton, ListItemIcon, ListItemText, Collapse } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import HotelIcon from '@mui/icons-material/Hotel';
import FlightIcon from '@mui/icons-material/Flight';
import DirectionsCarFilledIcon from '@mui/icons-material/DirectionsCarFilled';
import AttractionsIcon from '@mui/icons-material/Attractions';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import './Navbar.css';
import { getDatabase, ref, onValue } from "firebase/database";
import logo from '../media/logo.png';


const Navbar = () => {
  const [drawerOpen, setDrawerOpen] = useState(false);
  const [openServices, setOpenServices] = useState(false);
  const navigate = useNavigate();

  const isLoggedIn = localStorage.getItem('isLoggedIn') === 'true';
  const userEmail = localStorage.getItem('userEmail');
  const [categories, setCategories] = useState([]);

  const toggleDrawer = (open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setDrawerOpen(open);
  };

  const handleServicesClick = () => {
    setOpenServices(!openServices);
  };

  const handleNavigate = (path, categoryId = null) => {
    if (categoryId) {
        navigate(path, { state: { serviceType: categoryId } });
    } else {
        navigate(path);
    }
    setDrawerOpen(false); 
};


  const handleSignOut = () => {
    localStorage.removeItem('isLoggedIn');
    localStorage.removeItem('userEmail'); 
    navigate('/login'); 
  };

  useEffect(() => {
    const fetchCategories = () => {
      const db = getDatabase();
      const categoriesRef = ref(db, 'categories');
      onValue(categoriesRef, (snapshot) => {
        const data = snapshot.val();
        const fetchedCategories = [];
        for (let key in data) {
          fetchedCategories.push({
            id: key,
            name: data[key].name
          });
        }
        setCategories(fetchedCategories);
      });
    };
  
    fetchCategories();
  }, []);
  

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={toggleDrawer(true)}
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>

          <Typography variant="h6" sx={{ flexGrow: 1, display: 'flex', alignItems: 'center' }}>
            <Link to="/" style={{ display: 'flex', alignItems: 'center', color: 'inherit' }}>
              <img src={logo} alt="Logo" style={{ height: '30px' }} />
            </Link>
          </Typography>

          {isLoggedIn && userEmail && (
            <Typography variant="body2" sx={{ color: '#8BC34A' }}>
              Logged in as: <Link to="/profile" style={{ color: 'inherit', textDecoration: 'underline' }}>{userEmail}</Link>&nbsp;&nbsp;
            </Typography>
          )}
  
          <IconButton color="inherit" onClick={() => handleNavigate('/stays')}>
            <HotelIcon />
          </IconButton>
          <IconButton color="inherit" onClick={() => handleNavigate('/flights')}>
            <FlightIcon />
          </IconButton>
          <IconButton color="inherit" onClick={() => handleNavigate('/experiences')}>
            <AttractionsIcon />
          </IconButton>
          <IconButton color="inherit" onClick={() => handleNavigate('/car-rental')}>
            <DirectionsCarFilledIcon />
          </IconButton>
          {isLoggedIn ? (
            <Button color="inherit" onClick={handleSignOut}>Sign Out</Button>
          ) : (
            <Link to="/login" style={{ textDecoration: 'none', color: 'inherit' }}>
              <Button color="inherit">Login</Button>
            </Link>
          )}
        </Toolbar>
      </AppBar>
      <Drawer
        anchor="left"
        open={drawerOpen}
        onClose={toggleDrawer(false)}
        sx={{
          width: 250,
          '& .MuiDrawer-paper': {
            width: 250,
            boxSizing: 'border-box',
            backdropFilter: 'blur(8px)', 
            backgroundColor: 'rgba(0, 0, 0, 0.3)',
            display: 'flex',
            flexDirection: 'column',
          },
        }}
      >
        <div style={{ display: 'flex', flexDirection: 'column', height: '100%' }}>
          <List style={{ flexGrow: 1 }}>
            <ListItemButton onClick={() => handleNavigate('/')}>
              <ListItemIcon>
                <HomeIcon />
              </ListItemIcon>
              <ListItemText primary="Home" />
            </ListItemButton>
            <ListItemButton onClick={handleServicesClick}>
              <ListItemIcon>
                <FlightIcon />
              </ListItemIcon>
              <ListItemText primary="Services" />
              {openServices ? <ExpandLess /> : <ExpandMore />}
            </ListItemButton>
            <Collapse in={openServices} timeout="auto" unmountOnExit>
                <List component="div" disablePadding>
                    {categories.map((category) => (
                        <ListItemButton key={category.id} sx={{ pl: 11 }} onClick={() => handleNavigate('/search', category.id)} className="drawer-subitem">
                            <ListItemText primary={<Typography sx={{ fontStyle: 'italic' }}>{category.name}</Typography>} />
                        </ListItemButton>
                    ))}
                </List>
            </Collapse>
            <ListItemButton onClick={() => handleNavigate('/about')}>
              <ListItemIcon>
                <InfoIcon />
              </ListItemIcon>
              <ListItemText primary="About Us" />
            </ListItemButton>
          </List>
          <div className="drawer-footer">
            <Typography variant="caption" display="block">
              © 2024 BookIt
            </Typography>
          </div>
        </div>
      </Drawer>
    </>
  );  
};

export default Navbar;
