import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
  Box,
  Typography,
  Button,
  TextField,
  MenuItem,
  ThemeProvider,
  createTheme,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Grid,
  FormControl,
  InputLabel,
  Select
} from "@mui/material";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import CardActionArea from "@mui/material/CardActionArea";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { initializeApp } from "firebase/app";
import { getDatabase, ref, onValue } from "firebase/database";
import logo from '../media/logo.png';

// Custom dark theme for Material UI
const darkTheme = createTheme({
  palette: {
    mode: "dark",
    primary: {
      main: "#90caf9",
    },
    background: {
      default: "#121212",
      paper: "#1e1e1e",
    },
    text: {
      primary: "#e0e0e0",
      secondary: "#b3b3b3",
    },
  },
});

const LandingPage = () => {
  const [serviceType, setServiceType] = useState("");
  const [generalSearch, setGeneralSearch] = useState("");
  const [promotionalOffers, setPromotionalOffers] = useState([]);
  const [faqs, setFaqs] = useState([]);
  const [categories, setCategories] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const isLoggedIn = localStorage.getItem("isLoggedIn") === "true";
    if (!isLoggedIn) {
      navigate("/login");
    }

    const fetchCategories = () => {
      const categoriesRef = ref(getDatabase(), 'categories');
      onValue(categoriesRef, (snapshot) => {
        const data = snapshot.val();
        const categoryList = Object.keys(data).map(key => ({
          value: key,
          label: data[key].name,
        }));
        setCategories(categoryList);
      }, {
        onlyOnce: true
      });
    };

    const fetchServices = () => {
      const servicesRef = ref(getDatabase(), 'services');
      onValue(servicesRef, (snapshot) => {
        const data = snapshot.val();
        const servicesList = Object.keys(data).map(key => ({
          id: key,
          ...data[key]
        }));
        setPromotionalOffers(servicesList);
      }, {
        onlyOnce: true
      });
    };

    const fetchFAQs = () => {
      const faqsRef = ref(getDatabase(), 'faqs');
      onValue(faqsRef, (snapshot) => {
        const data = snapshot.val();
        const faqsList = Object.keys(data).map(key => ({
          id: key,
          ...data[key]
        }));
        setFaqs(faqsList);
      }, {
        onlyOnce: true
      });
    };

    fetchCategories();
    fetchServices();
    fetchFAQs();
  }, [navigate]);

  const handleGetStarted = () => {
    navigate("/search", { state: { serviceType } });
  };

  const handleGeneralSearchSubmit = (event) => {
    event.preventDefault();
    navigate("/search", { state: { searchQuery: generalSearch } });
  };

  const handleServiceTypeChange = (event) => {
    setServiceType(event.target.value);
  };

  const handleGeneralSearchChange = (event) => {
    setGeneralSearch(event.target.value);
  };

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
  };

  return (
    <ThemeProvider theme={darkTheme}>
      <Box sx={{ p: 20, bgcolor: "background.default", minHeight: "100vh" }}>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <img src={logo} alt="Logo" style={{ minWidth: "240px", maxWidth: "20%", marginBottom: "20px", marginTop: "-100px" }} />
        </div>
        <Typography variant="h3" gutterBottom textAlign="center" color="text.primary">
          Looking for your next adventure?
        </Typography>
        <Box sx={{ my: 4, display: "flex", justifyContent: "center" }}>
          <FormControl variant="outlined" sx={{ minWidth: 240, mr: 2 }}>
            <InputLabel>I'm looking for</InputLabel>
            <Select
              label="I'm looking for"
              value={serviceType}
              onChange={handleServiceTypeChange}
            >
              {categories.map((category) => (
                <MenuItem key={category.value} value={category.value}>
                  {category.label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
          <Button variant="contained" color="primary" onClick={handleGetStarted}>
            Go
          </Button>
        </Box>
        <form onSubmit={handleGeneralSearchSubmit} style={{ display: "flex", justifyContent: "center", marginBottom: "20px" }}>
          <TextField
            label="Search anything..."
            value={generalSearch}
            onChange={handleGeneralSearchChange}
            variant="outlined"
            sx={{ minWidth: 240, mr: 2 }}
          />
          <Button type="submit" variant="contained" color="primary">
            Search
          </Button>
        </form>
        <Typography variant="h5" gutterBottom color="text.primary">
          Special Offers
        </Typography>
        <Slider {...settings}>
          {promotionalOffers.map((offer, index) => (
            <Box key={index} sx={{ px: 2 }}>
              <Card sx={{ bgcolor: "background.paper" }}>
                <CardActionArea>
                  <CardMedia component="img" height="140" image={offer.imageUrl} alt={offer.title} />
                  <CardContent>
                    <Typography gutterBottom variant="h6" component="div" color="text.primary">
                      {offer.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {offer.description}
                    </Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            </Box>
          ))}
        </Slider>
        <Box mt={4}>
          <Typography variant="h5" gutterBottom color="text.primary">
            FAQs
          </Typography>
          {faqs.map((faq, index) => (
            <Accordion key={index} sx={{ bgcolor: "background.paper", mb: 2 }}>
              <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls={`panel${index}-content`} id={`panel${index}-header`}>
                <Typography variant="subtitle1" color="text.primary">
                  {faq.question}
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  {faq.answer}
                </Typography>
              </AccordionDetails>
            </Accordion>
          ))}
        </Box>
      </Box>
    </ThemeProvider>
  );
};

export default LandingPage;
