import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { getDatabase, ref, onValue } from 'firebase/database';
import { Box, Typography, Button } from '@mui/material';

const ServiceDetailPage = () => {
  const { serviceId } = useParams();
  const [service, setService] = useState(null);
  const navigate = useNavigate();
  const database = getDatabase();

  useEffect(() => {
    const serviceRef = ref(database, `services/${serviceId}`);
    onValue(serviceRef, (snapshot) => {
      setService(snapshot.val());
    }, {
      onlyOnce: true
    });
  }, [serviceId]);

  const handleBookAppointment = () => {
    // Navigate to booking page or open booking modal here
    // This example navigates to a booking page
    navigate(`/book-appointment/${serviceId}`);
  };

  if (!service) {
    return <Typography>Loading service details...</Typography>;
  }

  return (
    <Box sx={{ p: 4 }}>
      <Typography variant="h2">{service.name}</Typography>
      <Typography variant="body1">{service.description}</Typography>
      <Typography variant="h6">Price: ${service.price}</Typography>
      {/* Include other service details here */}
      <Button 
        variant="contained" 
        color="primary" 
        onClick={handleBookAppointment}
        sx={{ mt: 2 }}
      >
        Book Your Appointment
      </Button>
    </Box>
  );
};

export default ServiceDetailPage;
