import React, { useState, useEffect } from 'react';
import { useLocation, Link, useNavigate } from 'react-router-dom';
import { Box, Card, CardContent, Typography, TextField, Grid, Button } from '@mui/material';
import { getDatabase, ref, onValue } from "firebase/database";

const SearchPage = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const database = getDatabase();

  const [services, setServices] = useState([]);
  const serviceType = state?.serviceType || '';
  const searchQuery = state?.searchQuery || '';

  useEffect(() => {
    const fetchServices = () => {
      const servicesRef = ref(database, 'services');
      onValue(servicesRef, (snapshot) => {
        const servicesData = snapshot.val();
        const loadedServices = [];
        for (const key in servicesData) {
          const service = servicesData[key];
          if ((serviceType && service.category === serviceType) || 
              (searchQuery && (service.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                               service.description.toLowerCase().includes(searchQuery.toLowerCase())))) {
            loadedServices.push({ id: key, ...service });
          }
        }
        setServices(loadedServices);
      }, {
        onlyOnce: true
      });
    };
  
    if (!serviceType && !searchQuery) {
      navigate('/'); // Redirect to home if no search criteria are provided
    } else {
      fetchServices();
    }
  }, [serviceType, searchQuery, navigate, database]);  

  return (
    <Box sx={{ flexGrow: 1, p: 3 }}>
      <Typography variant="h4" gutterBottom>
        Search Results
      </Typography>
      <Grid container spacing={2}>
        {services.map((service) => (
          <Grid item xs={12} sm={6} md={4} key={service.id || service.name}> 
            <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
              <CardContent sx={{ flexGrow: 1 }}>
                <Link to={`/service/${service.id}`} style={{ textDecoration: 'none', color: 'inherit' }}>
                  <Typography gutterBottom variant="h5" component="div">
                    {service.name}
                  </Typography>
                </Link>
                <Typography variant="body2" color="text.secondary">
                  {service.description}
                </Typography>
              </CardContent>
              <Box sx={{ p: 2 }}>
                <Button
                  variant="contained"
                  fullWidth
                  component={Link}
                  to={`/service/${service.id}`}
                >
                  View Details
                </Button>
              </Box>
            </Card>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default SearchPage;
